@import '../../Styles/Basic/Basic.scss';

.wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;

  .title{
    margin: 20px 0;
    color: white;
  }

  .form{
    width: 100%;
  }

  .medicineWrapper{
    padding: 20px 5px;
    margin: 30px 0;
    background-color: rgba(whitesmoke, .5);
  }

  .medicineLabel{
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  .nameTitle{
    font-size: 22px;
  }

  .input{
    margin-left: 10px;
    padding: 10px 15px;
    background-color: transparent;
    border: none;
    border-left: 1px solid black;
    border-bottom: 1px solid black;
  }
  
  .inputAmount{
    @extend .input;
    width: 50px;
    padding-left: 20px;
    -moz-appearance: textfield;
  }

  .btnAdd{
    width: 60%;
    height: 50px;
    margin-top: 20px;
    letter-spacing: 2px;
    font-size: 20px;
    font-weight: 700;
    color: #a55f62;
    border: 2px solid black;
    background-color: black;
  }
}
